.container {
    background-color: #b7dac69f;
    border: 2px solid #FFCB05;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    height: 300px;
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    }
    
    img {
    width: 150px;
    height: 150px;
    padding-bottom: 10px;
    }
    
    p {
    margin: 2px;
    font-family: 'Arial', sans-serif;
    color: #333333;
    text-align: center;
    }

    .link {
      text-decoration: none;
    }
  
